.projects {
  padding-bottom: s(341);
  &__title {
    &-box {
      @include flex-50-y();
      justify-content: space-between;
    }
  }
  &__slider {
    padding: s(112) s(2) s(40) 0;
    &-btn {
      @include flex-50-50();
      @include block(s(53));
      border: s(0.5) solid var(--white);
      border-radius: 100%;
      @include transition(opacity, 1);
      & svg {
        @include block(s(20));
      }
      &.swiper-button-disabled {
        opacity: 0.4;
      }
      &-box {
        display: flex;
        gap: s(16);
        padding-bottom: s(9);
      }
      &--prev svg {
        transform: rotate(180deg);
      }
    }
  }
  &__slide {
    @include flex-50-50();
    cursor: pointer;
    height: s(260);
    border: s(1) solid var(--white);
    border-radius: s(10);
    background-color: transparent;
    transform: translateY(0);
    @include transitions((transform, background-color));
    &:hover {
      background-color: var(--red);
    }
    &.active {
      transform: translateY(-15%);
      background-color: var(--red);
    }
    & .frame {
      position: absolute;
      @include block(s(22));
      border-color: var(--white);
      border-style: solid;
      &-1,
      &-2 {
        top: s(-1);
      }
      &-1,
      &-3 {
        left: s(-1);
      }
      &-2,
      &-4 {
        right: s(-1);
      }
      &-3,
      &-4 {
        bottom: s(-1);
      }
      &-1 {
        border-width: s(3) 0 0 s(3);
        border-top-left-radius: s(9.5);
      }
      &-2 {
        border-width: s(3) s(3) 0 0;
        border-top-right-radius: s(9.5);
      }
      &-3 {
        border-width: 0 0 s(3) s(3);
        border-bottom-left-radius: s(9.5);
      }
      &-4 {
        border-width: 0 s(3) s(3) 0;
        border-bottom-right-radius: s(9.5);
      }
    }
    &:nth-child(2) .projects__logo {
      width: 47%;
    }
    &:nth-child(3) .projects__logo {
      width: 84%;
    }
  }
  &__logo {
    width: 60%;
  }
  &__content {
    border-radius: s(10);
    padding: s(40) s(63) s(41) s(59);
    background-color: var(--red);
    &-box {
      display: none;
      align-items: center;
      justify-content: space-between;
      &.active {
        display: flex;
      }
    }
  }
  &__name {
    display: inline-flex;
    align-items: center;
    text-transform: uppercase;
    word-wrap: break-word;
    overflow-wrap: anywhere;
    hyphens: auto;
    width: 100%;
    max-width: s(238);
    min-height: s(77);
    font-size: s(35);
    line-height: 110%;
  }
  &__desc {
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
    text-transform: uppercase;
    width: 100%;
    max-width: s(257);
    font-family: 'GraphikLCG', sans-serif;
    font-weight: 300;
    font-size: s(18);
    line-height: 120%;
    & span {
      display: inline-block;
    }
    & .underline {
      position: relative;
      margin-top: s(5);
      @include text-bg('w');
      font-family: 'EuropeExt', sans-serif;
      font-weight: 700;
      font-size: s(14);
      line-height: 160%;
      letter-spacing: 0.04em;
      &::after {
        position: absolute;
        content: '';
        bottom: s(-3);
        left: 0;
        @include block(100%, s(2));
        background-color: var(--white);
      }
    }
  }
  &__progress {
    display: inline-flex;
    flex-direction: column;
    &--count {
      @include text-bg('w');
      font-size: s(21);
      font-weight: 700;
      & span {
        @include text-bg('w');
        font-size: s(35);
        line-height: 110%;
      }
    }
    &--desc {
      font-family: 'GraphikLCG', sans-serif;
      font-size: s(20);
      font-weight: 300;
      line-height: 130%;
    }
  }
  @include mobile {
    padding-bottom: s-md(207.5);
    &__container {
      padding: 0 s-md(14);
    }
    &__title {
      margin-bottom: s-md(38);
      &-box {
        align-items: flex-start;
        flex-direction: column;
        padding: 0 s-md(2) s-md(21.5);
      }
    }
    &__slider {
      height: s-md(358);
      padding: s-md(3) s-md(2);
      &-btn {
        @include block(s-md(53));
        border-width: s-md(0.5);
        & svg {
          @include block(s-md(20));
        }
        &-box {
          gap: s-md(16);
          padding-bottom: 0;
        }
      }
    }
    &__slide {
      height: calc((100% - 30px) / 2);
      &.active {
        transform: translateY(0);
      }
      & .frame {
        @include block(s-md(19.7));
        &-1,
        &-2 {
          top: s-md(-2);
        }
        &-1,
        &-3 {
          left: s-md(-2);
        }
        &-2,
        &-4 {
          right: s-md(-2);
        }
        &-3,
        &-4 {
          bottom: s-md(-2);
        }
        &-1 {
          border-width: s-md(3) 0 0 s-md(3);
          border-top-left-radius: s-md(9.5);
        }
        &-2 {
          border-width: s-md(3) s-md(3) 0 0;
          border-top-right-radius: s-md(9.5);
        }
        &-3 {
          border-width: 0 0 s-md(3) s-md(3);
          border-bottom-left-radius: s-md(9.5);
        }
        &-4 {
          border-width: 0 s-md(3) s-md(3) 0;
          border-bottom-right-radius: s-md(9.5);
        }
      }
    }
    &__content {
      border-radius: s-md(10);
      margin: s-md(16) s-md(2) 0 s-md(2);
      padding: s-md(18) s-md(37) s-md(7.5) s-md(16);
      &-box {
        align-items: flex-start;
        flex-direction: column;
      }
    }
    &__name {
      max-width: initial;
      font-size: s-md(22);
      margin-bottom: s-md(20);
      line-height: 130%;
    }
    &__desc {
      max-width: initial;
      margin-bottom: s-md(21);
      font-size: s-md(14);
      & .underline {
        margin-top: s-md(6);
        font-size: s-md(12);
        &::after {
          bottom: 0;
        }
      }
    }
    &__progress {
      flex-direction: row;
      align-items: flex-end;
      gap: s-md(14);
      margin-bottom: s-md(18);
      &--count {
        font-size: s-md(17);
        line-height: 160%;
        & span {
          margin-right: s-md(-3);
          font-size: s-md(22);
          line-height: 150%;
        }
      }
      &--desc {
        padding-bottom: s-md(4);
        font-size: s-md(16);
        letter-spacing: 0.04em;
      }
    }
  }
}
