.us-title {
  text-transform: uppercase;
  font-weight: 400;
  font-size: s(82);
  line-height: 110%;
  color: var(--white);
  & .underline {
    position: relative;
    &::after {
      position: absolute;
      content: '';
      left: 0;
      bottom: s(8);
      @include block(100%, s(2));
      background-color: var(--white);
    }
  }
  @include mobile {
    font-size: s-md(29);
    & .underline {
      &::after {
        bottom: s-md(1);
        @include block(100%, s-md(1));
      }
    }
  }
}

.us-btn {
  text-transform: uppercase;
  border: s(1) solid var(--white);
  border-radius: s(1000);
  padding: s(14) s(23);
  letter-spacing: 0.04em;
  color: var(--white);
  @include mobile {
    padding: s-md(15) s-md(24);
    font-size: s-md(13);
    line-height: s-md(12);
  }
}

.us-link {
  transform: scale(1);
  color: var(--white);
  @include transitions((transform, color));
  &:hover,
  &:active {
    transform: scale(0.98);
    color: var(--red);
  }
}
